import React from 'react';
import './App.css';
import Header from "./components/header";
import {NameCard, ResumeCard, ServicesCard, ContactCard, BlogCard} from "./components/cards";
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";

function App() {
  return (
    <Router>
      <div className="container">
        <Header />  
        <Switch>
          <Route path="/" exact component={NameCard}></Route>
          <Route path="/resume" component={ResumeCard}></Route>
          <Route path="/services" component={ServicesCard}></Route>
          <Route path="/blog" component={BlogCard}></Route>
          <Route path="/contactme" component={ContactCard}></Route>
        </Switch>
        <footer class="page-footer">@copy2020.by <span>Jessie</span></footer>
      </div>
    </Router>
  );
}
export default App;